import Image from './Image'

class Technology {
    private image: Image;
    private name: string;
    private url: string;

    constructor(image: Image, name: string, url?:string) {
        this.image = image;
        this.name = name;
        this.url = url || "";
    }

    getName() {
        return this.name;
    }

    getUrl() {
        return this.url
    }

    getImage() {
        return this.image;
    }
}

export default Technology