import Image from './Image'

class SocialMedia {
    private name: string
    private url: string
    private image: Image

    constructor(name: string, url: string, image: Image) {
        this.name = name;
        this.url = url;
        this.image = image;
    }

    getName() {
        return this.name;
    }

    getUrl() {
        return this.url;
    }

    getImage() {
        return this.image;
    }
}

export default SocialMedia
