import React from 'react'
import '@/sass/project.scss';
import Card from 'components/layouts/Card';
import * as p from 'components/constant/Project';

interface IState {
    projects: string[]
}

class Project extends React.Component<{}, IState> {
    constructor(props: {}) {
        super(props)
        this.state = {
            projects: [
                p.LEMONHEXA,
                p.CEOUS,
                p.PATHLAB,
                p.INVITEMAN
            ],
        }
    }

    render() {
        return (
            <div id="project" className="project">
                <div className="container">
                    <div className="row justify-content-center">
                        <h2 className="header col-12">Project</h2>
                        <div className="projects row col-12">
                            {
                                this.state.projects.map(x => {
                                    const prjct = p.Projects[x]
                                    return (
                                        <div className="col-md-4 col-lg-3 project" key={x}>
                                            <Card image={prjct.getImage().getSource()} />
                                        </div>
                                    )
                                })
                            }
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Project