import React from 'react'

interface IProps {
    image?: string,
    content?: any,
}

class Card extends React.Component<IProps> {
    render() {
        return (
            <div className="card">
                {this.props.image && 
                    <div className="image">
                        <img src={this.props.image} className="mx-auto d-block" />
                    </div>
                }
                {this.props.content && 
                    <div className="content">{this.props.content}</div>
                }
            </div>
        )
    }
}

export default Card