import Image from './Image'

class Project {
    private name: string;
    private description: string;
    private url: string;
    private image: Image;

    constructor(image: Image, name: string, description?: string, url?: string) {
        this.image = image;
        this.name = name;
        this.description = description || "";
        this.url = url || "";
    }

    getImage() {
        return this.image;
    }

    getName() {
        return this.name;
    }

    getDescription() {
        return this.description;
    }

    getUrl() {
        return this.url;
    }
}

export default Project